@import url(https://fonts.googleapis.com/css?family=Lato&display=swap);

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}



body{
    background-color: #F0F6FF;
}
.header{
    font-size: 45px;
    line-height: 100px;
    font-weight: bold;
    color: black;
    text-align: center;
    margin:2px;
    padding:5px;
    font-family: 'Lato', sans-serif;
  }
.form{
    text-align: center;
    margin:5px;
}
.form-input{
    width:500px;
    padding:10px;
}
.form-btn{
    padding:10px;
    margin-left: 10px;
    color: #F0F6FF;
    border-radius: 7%;
    background-color:#00C14F;
}
.loading-content{
    text-align: center;
    font-size: 20px;
    font-weight: 500;
    margin-top: 20px;
}
.panel-title{
    text-align: center;
    margin-top: 10px;
    font-size: 30px;
    color:white;
    background-color:#599FA1;
}
.panel-title a{
    text-decoration: none;
    color:black;
}
.panel-body{
    display:flex;
    flex-direction: row;
    background-color:#CEF8FF;
}
.body-description{
    text-align: center;
    padding: 50px 25px 25px 25px;
    font-size: 20px;
    font-weight: 500;
}
.left-panel{
    width:30%;
    margin:5px;
    border: 2px solid black;
}
.left-panel img{
    border-bottom: 2px solid black;
}
.left-panel-description{
    font-weight: 500;
    text-align: center;
}
